body {
  font-family: 'Open Sans', sans-serif; }

.setup {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .setup .mdc-card {
    width: 65%;
    max-width: 900px;
    height: auto;
    min-height: 500px; }

.setup-card {
  display: flex; }
  .setup-card__form {
    width: 80%;
    height: auto;
    min-height: 500px;
    padding: 32px; }
    .setup-card__form form {
      margin-top: 16px; }
    .setup-card__form .mdc-text-field {
      width: 80%;
      margin-top: 24px;
      height: 40px; }
      .setup-card__form .mdc-text-field .mdc-text-field__input {
        font-size: 14px;
        padding: 7px 11px; }
      .setup-card__form .mdc-text-field .mdc-floating-label {
        font-size: 14px;
        top: 11px; }
      .setup-card__form .mdc-text-field .mdc-floating-label--float-above {
        transform: translateY(-96%) scale(0.75) !important; }
  .setup-card__description {
    width: 40%;
    height: auto;
    min-height: 500px;
    padding: 32px;
    display: flex;
    justify-content: center;
    align-items: center; }
  .setup-card__brand {
    color: #333648;
    font-weight: 700;
    font-size: 24px; }
  .setup-card__headline {
    color: #333648;
    font-weight: 400;
    font-size: 24px;
    padding-top: 16px;
    text-align: left; }
  .setup-card__body {
    color: #333648;
    font-weight: 400;
    font-size: 16px;
    margin-top: 8px; }
